
import ReactDOM from 'react-dom';

export function scrollIntoView(ctx) {
  ReactDOM.findDOMNode(ctx).scrollIntoView(false);
}

export const getViewport = () => ({
  width: (global.innerWidth || global.document.documentElement.clientWidth || global.document.body.clientWidth),
  height: (global.innerHeight || global.document.documentElement.clientHeight || global.document.body.clientHeight)
})



