
import React from 'react';
import cn from 'classnames';
import create from '../componentFactory';

import MAIN_TABS, {
  LOG_TAB,
  GOAL_TAB,
  TASK_TAB
} from '../../constants/main-tabs';

import Header from '../common/Header';
import TabBar from '../common/TabBar';

import EntryPane from '../core/EntryPane';
import GoalPane from '../core/GoalPane';
import TaskPane from '../core/TaskPane';

export default create({
  displayName: 'MobileLayout',
  propTypes: {
    mainTab: React.PropTypes.string.isRequired
  },
  render() {
    const { mainTab } = this.props;
    return <div id="MobileLayout" className={cn(
    )}>
      <Header isMobile={true} />
      <div id="BodyInner">
        <div className={cn(
          'h100',
          'w100'
        )}>
          {(() => {
            switch (mainTab) {
            case LOG_TAB:
              return <EntryPane />;
            case GOAL_TAB:
              return <GoalPane />;
            case TASK_TAB:
              return <TaskPane />;
            }
          })()}
        </div>
      </div>
    </div>
  }
});



