
import Task from '../connectors/TaskConnector';
import Goal from '../connectors/GoalConnector';

import { moveGoal } from '../redux/goals/actions';
import { moveTask } from '../redux/tasks/actions';

export const GOAL = 'GOAL';
export const TASK = 'TASK';

export const ComponentMap = {
  [TASK]: Task,
  [GOAL]: Goal
};

export const TargetActionMap = {
  [TASK]: moveTask,
  [GOAL]: moveGoal
}

export default [GOAL, TASK];



