
import React from 'react';
import cn from 'classnames';
import create from '../componentFactory';

import DropDownMenu from 'material-ui/DropDownMenu';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';

export default create({
  displayName: 'Header',
  propTypes: {
    isMobile: React.PropTypes.bool.isRequired
  },
  render() {
    return <div id="Header" className={cn(
      'border-bottom'
    )}>
      <div className={cn('h1', 'px2')}>
        <div className="flex flex-row justify-between items-center">
          <div className="flex-s-50">{'Myndfull'}</div>
          {this.props.isMobile &&
          <div className="flex-item">
            <DropDownMenu
              value={'CALL'}
              onChange={this.handleChange}
              underlineStyle={{ 'border-top': 'none' }}
              style={{ 'font-size': '1.3rem' }}
              iconStyle={{ 'right': 0 }}
            >
              <MenuItem value={'CALL'} primaryText={'GOAL'} />
              <MenuItem value={'sdf'} primaryText="Help" />
              <MenuItem value={'afs'} primaryText="Sign out" />
            </DropDownMenu>
          </div>}
        </div>
      </div>
    </div>
  }
});



